"use client";
import { useEffect, useState } from "react";
import Link from "next/link";
import { supabase } from "@/lib/supabaseClient";

export default function AdminDashboard() {
  const [counts, setCounts] = useState({ maquinas:0, tecnicos:0, ordenes:0, tareas:0 });

  async function cargar() {
    const [{ count: m }, { count: t }, { count: o }, { count: ta }] = await Promise.all([
      supabase.from("maquinas").select("*", { count: "exact" }).then(r=>r.count),
      supabase.from("usuarios").select("*", { count: "exact" }).eq("rol","tecnico").then(r=>r.count),
      supabase.from("ordenes").select("*", { count: "exact" }).then(r=>r.count),
      supabase.from("tareas").select("*", { count: "exact" }).then(r=>r.count)
    ]);
    setCounts({ maquinas: m||0, tecnicos: t||0, ordenes: o||0, tareas: ta||0 });
  }

  useEffect(()=>{ cargar(); }, []);

  return (
    <div className="p-6">
      <h1 className="text-3xl font-bold">Panel Admin</h1>
      <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mt-6">
        <div className="p-4 bg-white rounded shadow">
          <div className="text-sm text-gray-500">Máquinas</div>
          <div className="text-2xl font-bold">{counts.maquinas}</div>
          <Link href="/admin/maquinas" className="text-blue-600 text-sm">Ver</Link>
        </div>
        <div className="p-4 bg-white rounded shadow">
          <div className="text-sm text-gray-500">Técnicos</div>
          <div className="text-2xl font-bold">{counts.tecnicos}</div>
          <Link href="/admin/tecnicos" className="text-blue-600 text-sm">Ver</Link>
        </div>
        <div className="p-4 bg-white rounded shadow">
          <div className="text-sm text-gray-500">Órdenes</div>
          <div className="text-2xl font-bold">{counts.ordenes}</div>
          <Link href="/admin/ordenes" className="text-blue-600 text-sm">Ver</Link>
        </div>
        <div className="p-4 bg-white rounded shadow">
          <div className="text-sm text-gray-500">Tareas</div>
          <div className="text-2xl font-bold">{counts.tareas}</div>
          <Link href="/admin/tareas" className="text-blue-600 text-sm">Ver</Link>
        </div>
      </div>

      <div className="mt-6">
        <Link href="/admin/mapa" className="px-3 py-2 bg-indigo-600 text-white rounded">Mapa Tiempo Real</Link>
      </div>
    </div>
  );
}
